<template>
  <div class="page page-field">
    <mt-header :title="title">
      <router-link to="/home" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>

    <div class="page-content">
      <div class="page-inner page-field-inner">
        <div class="page-part">
          <mt-field label="" placeholder="" :attr="{ maxlength: 10 }"></mt-field>
          <mt-field label="" placeholder="" type="email"></mt-field>
          <mt-field label="" placeholder="" type="password"></mt-field>
          <mt-field label="" placeholder="" type="tel"></mt-field>
          <mt-field label="" placeholder="" type="url"></mt-field>
          <mt-field label="" placeholder="" type="number"></mt-field>
          <mt-field label="" placeholder="" type="date"></mt-field>
          <mt-field label="" placeholder="" type="textarea" rows="4"></mt-field>
        </div>

        <div class="page-part">
          <mt-field placeholder="" type="email"></mt-field>
          <mt-field placeholder="" type="password"></mt-field>
        </div>

        <div class="page-part">
          <mt-field label="" placeholder="" state="success"></mt-field>
          <mt-field label="" placeholder="" state="error"></mt-field>
          <mt-field label="" placeholder="" state="warning"></mt-field>
        </div>

        <div class="page-part">
          <mt-field label="" placeholder="">
            <img src="../assets/100x100.png" height="48px" width="100px">
          </mt-field>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'page-field',
  data() {
    return {
      title: this.$route.name
    }
  }
};
</script>



